import StatCard from "./StatCard";
import LineChartCard from "./LineChartCard";
import PieChartCard from "./PieChartCard";
import BarChartCard from "./BarChartCard";
import RecentActivity from "./RecentActivity";

import { ActivityDummy, StatsDummy, LineData, PieData, BarData } from "@/mock/dashboard-data";

import { TrendingUp, Users, Briefcase, DollarSign, AlertTriangle } from "lucide-react";

export default function DashboardWidgets() {
  return (
    <div className="space-y-6">

      {/* STAT CARDS */}
      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-5 gap-4">
        <StatCard
          title="Total Bookings"
          value={StatsDummy.totalBookings}
          percent="+12.1%"
          icon={<TrendingUp size={20} />}
          positive
        />

        <StatCard
          title="Pending Providers"
          value={StatsDummy.pendingProviders}
          percent="+7.2%"
          icon={<Briefcase size={20} />}
          positive
        />

        <StatCard
          title="Active Customers"
          value={StatsDummy.activeCustomers}
          percent="+3.2%"
          icon={<Users size={20} />}
          positive
        />

        <StatCard
          title="Total Revenue"
          value={`$${StatsDummy.totalRevenue.toLocaleString()}`}
          percent="+15.7%"
          icon={<DollarSign size={20} />}
          positive
        />

        <StatCard
          title="Active Disputes"
          value={StatsDummy.activeDisputes}
          percent="-5.4%"
          icon={<AlertTriangle size={20} />}
          positive={false}
        />
      </div>

      {/* MAIN GRID */}
      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">

        {/* LEFT SIDE (Big charts) */}
        <div className="lg:col-span-2 space-y-6">
          <LineChartCard title="Bookings Over Time" data={LineData.bookings} />
          <BarChartCard title="Revenue by City" data={BarData} />
        </div>

        {/* RIGHT SIDE */}
        <div className="space-y-6">
          <PieChartCard title="Services" data={PieData} />
          <LineChartCard
            title="Customer Growth"
            data={LineData.customers}
            lineColor="#10b981"
          />
        </div>
      </div>

      {/* ACTIVITY */}
      <RecentActivity items={ActivityDummy} />
    </div>
  );
}
